import { Component, OnInit } from '@angular/core';
import { User } from '../user';
import { UserService } from '../user.service';
import { Phone } from '../phone';
import { Address } from '../address';
import { Username } from '../username';
import { ValueTransformer } from '@angular/compiler/src/util';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.css']
})
export class UsersComponent implements OnInit {

  users: User[];
  username: Username;

  constructor(private userService: UserService) { }

  ngOnInit() {
    this.getUsers();
  }

  getUsers(): void {
    this.userService.getUsers()
    .subscribe(users => this.users = users);
    }

  add(name: string, age: number, number: string, street: string): void {
    // number = number.trim();
    // street = street.trim();

    if (!name || !age || !number || !street) { return; }

    const newuser = new User();
    newuser.name = name;
    newuser.age = age;
    newuser.phone = new Phone(number);
    newuser.address = new Address(street);

    this.userService.addUser(newuser as User)
      .subscribe(user => {
        this.users.push(newuser);
      });
  }

  getUserByID(id: number): any {
    return this.userService.getUserNo404(id).subscribe(val => { this.username = val; });
  }

}
